body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

#main {
  padding-left: 10px;
  padding-right: 10px;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.download-btn {
  text-transform: uppercase;
}

.flexbox {
  display: flex;
  flex-direction: row;
}

.flexbox > span {
  width: 33.33%;
  display: block;
}

.flexbox > span:nth-child(2) {
  width: 33.33%;
  text-align: center;
}

.flexbox > span:nth-child(3) {
  width: 33.33%;
  text-align: right;
}

#chart_footer {
  display: none;
}